.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.nice-icon{
  padding: 10px;
}

.carousel.slide {
   margin: 0 auto;  /*important */
   min-width: 100%;
}


.ending{
  height: 100%;
  padding: 10%;
}
  
.container-color{
  background-color: #343a40;
  min-width: 1600px;
  text-align: center;
  padding: 4%;
}

.carousel-inner img {
  margin: auto;
}

.image-holder{
  width: 1300px;
  margin: auto;
  text-align: center;
  height: 1000px;
  background-color: #343a40;
}

.carousel-image{
  margin: auto;
  max-height: 100%;
  max-width: 100%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body {
  background: linear-gradient(90deg, #fcff9e 0%, #c67700 100%);
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}


@media only screen and (max-width: 600px) {
.container-color{
  background-color: #343a40;
  min-width: 100%;
  text-align: center;
  padding: 4%;
}

.carousel.slide {
   margin: 0 auto;  /*important */
   min-width: 100%;
}

.image-holder{
  margin: auto;
  width: 100%;
  height: 400px;
  background-color: #343a40;
}
}